import React,{useContext, useState} from 'react'
import { GlobalContext } from '../context/GlobalState';


export default function AddTranation() {
     const [text,setText]=useState('');
     const [amount,setAmount]=useState(0);
     const {addTransaction}=useContext(GlobalContext);
     const onSubmit=(e)=>{
          e.preventDefault();
          const newTransaction={
               id:performance.now().toString(36)+Math.random().toString(36).substr(2),
               text,
               amount:+amount
``          }
          addTransaction(newTransaction);
     }
     return (
          <>
            <h3>Add new transaction</h3>
            <form onSubmit={onSubmit}>
                 <div className="form-control">
                      <label htmlFor="text">Text</label>
                      <input type="text" value={text} onChange={(e)=> setText(e.target.value)} placeholder="Enter
                      text..." />
                 </div>
                 <div className="form-control">
                      <label htmlFor="amount">Amount <br />
                           (negative - expense, positive - income)</label>
                       <input type="number" value={amount}  onChange={(e)=> setAmount(e.target.value)} placeholder="Enter
                      amount..." />
                 </div>
                 <button className="btn">Add transaction</button>
            </form>
               
          </>
     )
}
